ul {
    list-style-type: none;
    padding: 0;
}

li {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: 4rem 0;
    text-align: center;
}

li img {
    width: 40rem;
}

li div {
    width: 40rem;
}